#main-page {
    background-color: #2e302e;
    color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
}

h1 {
    margin-top: 20px;
    font-size: 2.5rem;
    text-align: center;
}

.product-content {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    margin-top: 1%;
}

.product-content > div {
    flex: 1 1 calc(33.333% - 10px); /* 3 items per row */
    box-sizing: border-box;
    max-width: calc(33.333% - 10px);
}

@media (max-width: 768px) {
    h1 {
        font-size: 2rem;
    }

    .product-content > div {
        flex: 1 1 calc(50% - 10px); /* 2 items per row */
        max-width: calc(50% - 10px);
    }
}

@media (max-width: 480px) {
    h1 {
        font-size: 1.5rem;
    }

    .product-content > div {
        flex: 1 1 100%; /* 1 item per row */
        max-width: 100%;
    }

    .button {
        font-size: 1rem;
        padding: 10px 15px;
    }
}
